___CSS_LOADER_EXPORT___.push([module.id, `body,html{
    margin:0;
    padding:0;
}

body{
    background-color: #C4DBE0 ;
    color: white;
}

.header{
    font-size: 80px;
    font-weight: bold;
    text-shadow: 4px 4px 4px #002c66;
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    display: flex;
    justify-content: center;
}
.boards{
    display: flex;
    gap: 50px;
    justify-content: center;
    margin-top: 50px;
}

div[class^="column"]{
    border: 2px solid grey;
}
[class^="row"]{
    display: grid;
    grid-template-columns: repeat(10,1fr);
}
.playerBoard, .computerBoard{
    height: 400px;
    width: 400px;
    background-color: #002c66;
    border: 2px solid grey;
    display: grid;
    grid-template-rows: repeat(10,1fr);
}

.playerBoard .ship{
    background-color: rgb(175, 210, 255);
}

.boards .attacked{
    background-color: rgba(255, 0, 0, 0.445);
}
.boards .attacked.ship{
    background-color: rgb(37, 247, 9);
}

.display{
    height: 30px;
    width: 150px;
    color: rgb(228, 0, 0);
    text-align: center;
    text-shadow: 3px 3px 3px rgba(0, 0, 255, 0.377);
    font-size: 23px;
    font-weight: 700;
    font-style:italic;
    
    align-self: center;

    padding: 20px 10px;
    padding-bottom: 20px;
    border-bottom: 3px solid white;
    border-top: 3px solid white;
}`, "",{"version":3,"sources":["webpack://./src/styles/mainStyle.css"],"names":[],"mappings":"AAAA;IACI,QAAQ;IACR,SAAS;AACb;;AAEA;IACI,0BAA0B;IAC1B,YAAY;AAChB;;AAEA;IACI,eAAe;IACf,iBAAiB;IACjB,gCAAgC;IAChC,kJAAkJ;IAClJ,aAAa;IACb,uBAAuB;AAC3B;AACA;IACI,aAAa;IACb,SAAS;IACT,uBAAuB;IACvB,gBAAgB;AACpB;;AAEA;IACI,sBAAsB;AAC1B;AACA;IACI,aAAa;IACb,qCAAqC;AACzC;AACA;IACI,aAAa;IACb,YAAY;IACZ,yBAAyB;IACzB,sBAAsB;IACtB,aAAa;IACb,kCAAkC;AACtC;;AAEA;IACI,oCAAoC;AACxC;;AAEA;IACI,wCAAwC;AAC5C;AACA;IACI,iCAAiC;AACrC;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,qBAAqB;IACrB,kBAAkB;IAClB,+CAA+C;IAC/C,eAAe;IACf,gBAAgB;IAChB,iBAAiB;;IAEjB,kBAAkB;;IAElB,kBAAkB;IAClB,oBAAoB;IACpB,8BAA8B;IAC9B,2BAA2B;AAC/B","sourcesContent":["body,html{\n    margin:0;\n    padding:0;\n}\n\nbody{\n    background-color: #C4DBE0 ;\n    color: white;\n}\n\n.header{\n    font-size: 80px;\n    font-weight: bold;\n    text-shadow: 4px 4px 4px #002c66;\n    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;\n    display: flex;\n    justify-content: center;\n}\n.boards{\n    display: flex;\n    gap: 50px;\n    justify-content: center;\n    margin-top: 50px;\n}\n\ndiv[class^=\"column\"]{\n    border: 2px solid grey;\n}\n[class^=\"row\"]{\n    display: grid;\n    grid-template-columns: repeat(10,1fr);\n}\n.playerBoard, .computerBoard{\n    height: 400px;\n    width: 400px;\n    background-color: #002c66;\n    border: 2px solid grey;\n    display: grid;\n    grid-template-rows: repeat(10,1fr);\n}\n\n.playerBoard .ship{\n    background-color: rgb(175, 210, 255);\n}\n\n.boards .attacked{\n    background-color: rgba(255, 0, 0, 0.445);\n}\n.boards .attacked.ship{\n    background-color: rgb(37, 247, 9);\n}\n\n.display{\n    height: 30px;\n    width: 150px;\n    color: rgb(228, 0, 0);\n    text-align: center;\n    text-shadow: 3px 3px 3px rgba(0, 0, 255, 0.377);\n    font-size: 23px;\n    font-weight: 700;\n    font-style:italic;\n    \n    a